import React from "react";
import {NavLink} from "react-router-dom";
import Burger from "./Burger.jsx";

function Header() {
    return (
        <header>
            <nav>
                <Burger />
                <ul className="wideNav">
                    <NavLink to="/fabianlarries" className="menu1" activeClassName="sel" exact><li>HOME</li></NavLink>
                    <NavLink to="/fabianlarries/about" className="menu1" activeClassName="sel"><li>ABOUT</li></NavLink>
                    <NavLink to="/fabianlarries/nextevents" className="menu1" activeClassName="sel"><li>NEXT EVENTS</li></NavLink>
                    <NavLink to="/fabianlarries/songs" className="menu1" activeClassName="sel"><li>SONGS</li></NavLink>
                    <NavLink to="/fabianlarries/news" className="menu1" activeClassName="sel"><li>NEWSLETTER</li></NavLink>
                    <NavLink to="/fabianlarries/contacts" className="menu1" activeClassName="sel"><li>CONTACTS</li></NavLink>
                </ul>
            </nav>
        </header>
    );
};

export default Header;